*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/WeatherApp/static/media/sky-g6d0a50f64_1280.d9b2d03d.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App input, .App button {
  font-size: 25px;
  position: absolute;
  padding: 10px 20px;
  border: 2px solid rgb(94, 22, 70);
  background-color: #fff;
  transition: .3s;
  background-color:rgb(255, 255, 255);
}

.App input{
  top: 10%;
  left: 30%;
  height: 50px;
  width:350px
}

.App button{
  height: 50px;
  top:10%;
  left:55%
}

.App button {
  cursor: pointer;
}

.App button:hover {
  background-color: black;
  color: white;
  border-color: black;
}
.result {
  width: 100vw;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  color: white;

}

.result p,
.result h2 {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.result h2 {
  font-size: 100px;
  top: 150px;

}

.result p.temp {
  font-size: 80px;
  top: 270px;
}

.result p.sunrise {
  font-size: 25px;
  top: 390px;
}

.result p.sunset {
  font-size: 25px;
  top: 450px;
}

.result p.pressure {
  font-size: 25px;
  top: 500px;
}

.result p.wind {
  font-size: 25px;
  top: 550px;
}
